<template>
    <div class="ion-card" :class="[modeClass,colorClass]">
        <slot></slot>
    </div>
</template>
<style lang="scss">
    @import "card.scss";
    @import "card.ios.scss";
    @import "card.md.scss";
</style>
<script type="text/javascript">
  /**
   * @component Card
   * @description
   *
   * ##  / Card
   *
   *
   * ### 
   *
   * , img width: 100%
   * ```
   * <Card>
   *    <img src="">
   *    <CardHeader>
   *         <CardTitle>CardTitle</CardTitle>
   *    </CardHeader>
   *    <CardContent>
   *        <CardTitle>CardTitle</CardTitle>
   *        <p>CardContent</p>
   *    </CardContent>
   * </Card>
   * ```
   *
   * #### CardHeader
   *
   * Card, padding. , .
   *
   * #### CardTitle
   *
   * title, 
   *
   * #### CardContent
   *
   * padding, . , , .
   *
   *
   *
   *
   * ### 
   * ```
   * // 
   * import { Card, CardContent, CardHeader, CardTitle } from 'vimo/components/card'
   * // 
   * Vue.component(Card.name, Card)
   * Vue.component(CardContent.name, CardContent)
   * Vue.component(CardHeader.name, CardHeader)
   * Vue.component(CardTitle.name, CardTitle)
   * // 
   * export default{
   *   components: {
   *     Card, CardContent, CardHeader, CardTitle
   *  }
   * }
   * ```
   *
   * @slot  - 
   *
   * @demo http://xiangsongtao.com/vimo/#/cards
   *
   * */
  import mixin from './mixin'
  export default{
    name: 'Card',
    data () {
      return {
        componentName: 'card'
      }
    },
    extends: mixin
  }
</script>



